<template>
  <q-drawer
    mini
    bordered
    show-if-above
    side="left"
    behavior="desktop"
    class="drawer-main"
    v-model="$root.mainDrawer"
    :breakpoint="mainDrawerBreakpoint"
    :mini-width="$root.mainDrawerCfg.mini"
    :width="$root.mainDrawerCfg.width"
  >
    <q-scroll-area class="fit">
      <q-list>
        <div class="flex flex-center q-mt-lg">
          <q-item
            clickable
            to="/"
            class="bg-none text-primary rounded bg-transparent"
          >
            <q-item-section avatar>
              <GrowmodoTextIcon size="32px" :text="false" />
            </q-item-section>
          </q-item>
        </div>

        <div class="flex flex-center q-pt-lg q-pb-sm">
          <q-item
            clickable
            class="menu-item cursor-pointer text-primary bg-none"
            @click="$root.toggleRequest()"
          >
            <q-item-section avatar>
              <q-avatar
                rounded
                size="44px"
                class="rounded text-white bg-primary"
              >
                <q-icon
                  name="icon-plus"
                  color="white"
                  size="sm"
                  class="q-pa-xs"
                  style="color: white !important"
                />
              </q-avatar>
              <tooltip-pro
                :offset="[8, 8]"
                self="center left"
                anchor="center right"
              >
                Add a Request
              </tooltip-pro>
            </q-item-section>
          </q-item>
        </div>

        <div class="flex flex-center q-my-md">
          <q-item
            clickable
            v-for="item in essentialLinks"
            :key="item"
            v-ripple:secondary
            class="menu-item q-my-sm"
            :class="item.class"
            :to="item.link"
            :href="item.href"
            @click="item.action"
          >
            <q-item-section v-if="item.icon" avatar>
              <q-icon size="sm" :name="item.icon" class="color-primary2" />
            </q-item-section>

            <q-item-section>
              <q-item-label>{{ item.title }}</q-item-label>
            </q-item-section>

            <tooltip-pro
              :offset="[8, 8]"
              self="center left"
              anchor="center right"
            >
              {{ item.title }}
            </tooltip-pro>
          </q-item>
        </div>

        <div
          class="flex flex-center q-mb-md"
          :style="`padding-top: calc(100vh - ${settingsPaddingTop}px)`"
        >
          <q-item
            clickable
            class="menu-item settings"
            :to="$route.params?.settingsTab ? undefined : '/settings'"
            @click="$root.toggleSettingsDrawer()"
          >
            <q-item-section avatar>
              <q-icon
                size="sm"
                name="icon-settings-02"
                class="color-primary2"
              />
            </q-item-section>
            <q-item-section>Account Settings</q-item-section>
          </q-item>
        </div>
      </q-list>
    </q-scroll-area>
  </q-drawer>
</template>

<script>
import { defineComponent } from 'vue';
import GrowmodoTextIcon from 'src/components/GrowmodoTextIcon.vue';
import { openURL } from 'quasar';
import { mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'DrawerMain',

  components: { GrowmodoTextIcon },

  setup() {
    return {
      openURL: openURL,
      mainDrawerBreakpoint: 600,
      essentialLinks: [],
    };
  },

  computed: {
    ...mapState(useGrowmodo, ['externalLinks']),

    settingsPaddingTop() {
      return 58 * this.essentialLinks.length + 550;
    },
  },

  mounted() {
    this.essentialLinks = [
      {
        title: 'Home',
        icon: 'icon-home-line',
        link: '/',
        class: 'home',
      },
      {
        title: 'Workspace',
        icon: 'icon-grid-01',
        link: '/workspace',
      },
      {
        title: 'Files',
        icon: 'icon-folder',
        link: '/files',
      },
      {
        title: 'Help Center',
        icon: 'icon-message-question-square',
        action: () => openURL(this.externalLinks?.help_link),
      },
      // {
      //   title: 'Discover',
      //   icon: 'icon-compass-03',
      //   link: '/discover',
      // },
      {
        title: 'Get a Gift',
        icon: 'icon-gift-01',
        link: '/gift',
      },
    ];
  },

  watch: {
    ['$root.$q.screen.width']: {
      async handler(val) {
        if (val <= this.mainDrawerBreakpoint && this.$root.mainDrawer)
          this.$root.toggleMainDrawer(false);
        else if (val > this.mainDrawerBreakpoint && !this.$root.mainDrawer)
          this.$root.toggleMainDrawer(true);
      },
    },
  },
});
</script>
