                          @click="toggleAnsArray('q9', item)"
                        >
                          <template v-slot:avatar>
                            <q-checkbox
                              size="lg"
                              autofocus
                              v-model="questions.q9"
                              :val="item"
                              color="secondary"
                              checked-icon="icon-check-square"
                              unchecked-icon="icon-square "
                              indeterminate-icon="icon-square text-secondary text-h5"
                              :name="item"
                            />
                          </template>
                          <template v-slot:title>
                            <div
                              :class="
                                questions.q9.includes(item)
                                  ? 'text-secondary'
                                  : 'text-primary'
                              "
                            >
                              {{ item }}
                            </div>
                          </template>
                        </card-item>
                      </div>
                    </div>
                  </q-list>
                </q-card-section>
              </q-card>
            </q-step>
          </q-stepper>

          <!-- Navigation -->
          <div v-if="formStep > 1" class="full-width text-center q-mt-lg">
            <div class="flex flex-center q-col-gutter-md">
              <div
                v-if="formStep > 2"
                class="full-width"
                style="max-width: 240px"
              >
                <q-btn
                  no-caps
                  outline
                  unelevated
                  color="primary"
                  class="rounded full-width slide-hover full-width hover-shadow-gm q-py-sm"
                  @click="prevButtonClicked()"
                >
                  <q-icon
                    size="sm"
                    name="icon-arrow-narrow-left"
                    class="q-pl-sm"
                    :class="!onboard_submitting ? 'slide-hover-right-md' : ''"
                  />
                  <span class="q-px-sm text-body1 font-medium"> Back </span>
                </q-btn>
              </div>
              <div
                class="full-width"
                :style="`max-width: ${formStep > 2 ? '240px' : '180px'}`"
              >
                <q-btn
                  no-caps
                  unelevated
                  color="primary"
                  class="rounded full-width slide-hover full-width hover-shadow-gm q-py-sm"
                  :disable="onboard_submitting"
                  @click="
                    formStep < 10 ? nextButtonClicked() : submitFormAction()
                  "
                >
                  <span class="q-pl-sm text-body1 font-medium">{{
                    formStep < 10 ? 'Continue' : 'Get Started'
                  }}</span>
                  <q-icon
                    size="sm"
                    name="icon-arrow-narrow-right"
                    class="q-px-sm"
                    :class="!onboard_submitting ? 'slide-hover-left-md' : ''"
                  />
                </q-btn>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <q-dialog persistent v-model="onboarded">
      <q-card
        flat
        style="width: 980px; max-width: 80vw"
        class="text-center q-pa-lg"
      >
        <q-card-section>
          <CardIconBox icon="icon-check-circle-broken" iconSize="md" />
        </q-card-section>
        <q-card-section class="q-gutter-sm">
          <div class="text-h5 font-medium">
            Thank You. Your account has already been set up and is ready to use.
          </div>
          <div class="text-body1 color-primary2">
            Your maintenance plan is active, you are able to request
            maintanenance tasks.
          </div>
        </q-card-section>
        <!-- Navigation -->
        <q-card-actions class="q-pa-md">
          <div class="full-width text-center q-gutter-md">
            <q-btn
              no-caps
              unelevated
              :ripple="{ color: 'accent' }"
              color="primary"
              class="rounded slide-hover hover-shadow-gm q-py-md"
              :disable="onboard_submitting"
              v-close-popup
              @click="
                () => {
                  $router.push('/');
                  $emit('exit');
                }
              "
            >
              <span class="q-pl-sm">Go to homepage</span>
              <q-icon
                size="sm"
                name="icon-arrow-narrow-right"
                class="q-px-sm"
                :class="!onboard_submitting ? 'slide-hover-left-md' : ''"
              />
            </q-btn>
          </div>
        </q-card-actions>
      </q-card>
    </q-dialog>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';
import CardItem from '../Globals/CardItem.vue';
import useGrowmodo from 'stores/growmodo';
import useUser from 'stores/user';
import { mapActions } from 'pinia';
import { extractErrorMessageString } from 'src/assets/scripts/functions.js';

export default defineComponent({
  name: 'OnBoarding',

  emits: ['start', 'done', 'exit'],

  props: {
    noclose: {
      type: Boolean,
      default: false,
    },
  },

  components: { CardIconBox, CardItem },

  data() {
    return {
      formStep: 1,
      onboard_submitting: false,
      onboarded: false,
      questions: {
        q1: [],
        q2: '',
        q3: '',
        q4: '',
        q5: '',
        q6: [],
        q7: [],
        q8: [],
        q9: [],
      },
    };
  },

  watch: {
    formStep: {
      async handler(val) {
        if (val > 2) {
          // Check if question has an answer
          if (
            !this.questions?.[`q${val - 2}`] ||
            !this.questions?.[`q${val - 2}`]?.length
          ) {
            this.notifySelectOption();
            this.formStep--;
          }
        }
      },
    },
    onboarded: {
      async handler(val) {
        if (val) this.$emit('done');
      },
    },
  },

  methods: {
    ...mapActions(useGrowmodo, ['formatPlatformIconSrc']),
    ...mapActions(useUser, ['onboardUser']),

    start(e) {
      this.$emit('start', e);
      if (this.formStep < 2) this.formStep++;
    },

    prevButtonClicked() {
      this.$refs?.['formStepper']?.previous?.();
    },

    nextButtonClicked() {
      this.$refs?.['formStepper']?.next?.();
    },

    async submitFormAction() {
      this.onboard_submitting = true;
      if (!this.questions.q9 || !this.questions.q9?.length) {
        this.notifySelectOption();
      } else {
        const _ = this.questions;
        const payload = {
          questions: {
            answer_1: _.q1,
            answer_2: _.q2,
            answer_3: _.q3,
            answer_4: _.q4,
            answer_5: _.q5,
            answer_6: _.q6,
            answer_7: _.q7,
            answer_8: _.q8,
            answer_9: _.q9,
          },
        };
        const response = await this.onboardUser(payload);
        if (response.success) {
          this.nextButtonClicked();
          this.onboarded = true;
        } else {
          const message = extractErrorMessageString(response);
          this.$q.notify({
            icon: 'icon-alert-triangle',
            iconColor: 'negative',
            message: message || 'Error while submitting your response',
          });
        }
      }
      this.onboard_submitting = false;
    },

    notifySelectOption() {
      this.$q.notify({
        icon: 'icon-alert-triangle',
        iconColor: 'negative',
        message: 'Please answer the question.',
      });
    },

    toggleAnsArray(questionsKey, labelVal) {
      let labelValIndex = this.questions?.[questionsKey]?.indexOf?.(labelVal);
      if (labelValIndex === -1) {
        this.questions?.[questionsKey].push(labelVal);
      } else {
        this.questions?.[questionsKey].splice(labelValIndex, 1);
      }
    },
  },

  beforeUnmount() {
    this.$emit('exit');
  },
});
</script>

