import useGrowmodo from 'stores/growmodo';
import useUser from 'stores/user';
import config from 'src/assets/_config';
import { Editor_Mixin } from 'src/mixins';

export default defineComponent({
  name: 'FormTaskCreate',

  mixins: [Editor_Mixin],

  components: { CardIconBox, CreateNewBrand, GrowmodoTextIcon },

  props: {
    noclose: {
      type: Boolean,
      default: false,
    },
  },

  setup() {
    const $q = useQuasar();
    const descriptionEditorRef = ref(null);

    $q.iconMapFn = (iconName) => {
      const icon = config.icons?.stepper[iconName];
      if (icon !== void 0) {
        return { icon: `img:${icon}` };
      }
    };

    return { descriptionEditorRef };
  },

  data() {
    const task_data = {
      1: {
        id: 1,
        label: 'Description',
        key: 'description',
        description:
          ' What is the problem you want to solve? What do you try to achieve? E.g. Increasing the conversion rate or improving the UX.',
        required: true,
      },
      2: {
        id: 2,
        label: 'Target Audience',
        key: 'audience',
        description: 'Who do we really work for? Who is the end-user?',
        required: true,
      },
      3: {
        id: 3,
        label: 'Priority',
        description:
          'How important is this task compared to the other tasks in the queue? You can tell us later in the task comment when priorities change.',
        required: true,
      },
      4: {
        id: 4,
        label: 'Select Platform',
        key: 'platform',
        description:
          'If you want us to develop a page or website, please choose a platform you want us to use?',
        required: true,
      },
      5: {
        id: 5,
        label: 'Video Walkthrough',
        key: 'walkthrough',
        description: 'Please provide a screen-recording video link.',
        required: false,
      },
      6: {
        id: 6,
        label: 'Warning',
        key: 'warning',
        description:
          'What do you don’t want us to do? What do we need to be aware of?',
        required: false,
      },
      7: {
        id: 7,
        label: 'Brand',
        key: 'brand',
        description: 'Please select the brand where you want to add this task.',
        required: true,
      },
      8: {
        id: 8,
        label: 'Resources',
        key: 'resources',
        description:
          'Files/Login details needed to complete the task. Please share them in a Google Drive or Dropbox folder that everybody can view through your link.',
        required: true,
      },
    };
    return {
      step: 1,
      task: { 1: '' },
      task_data,
      prevTitle: document.title,
      addBrand: false,
      task_submitting: false,
      task_submitted: false,
      taskPriorities: [],
      goHome: false,
      containerSideMenu: false,
      filteredBrandsOptions: [],
      openURL,
      checker,
    };
  },

  async mounted() {
    this.prevTitle = this.$root?.config?.metaData?.title;
    this.$root.setMeta({ title: this.taskInfo.label });
    // Get The Priorities
    this.taskPriorities = await this.getCustomField({
      name: 'Priority',
      key: 'enum_options',
    });
  },

  computed: {
    ...mapState(useAsana, ['brand', 'projects', 'assignee', 'custom_fields']),
    ...mapState(useUser, ['selectedOrgBrands']),
    ...mapState(useGrowmodo, ['brandCategories']),

    taskInfo() {
      return this.$root.task.info;
    },
  },

  watch: {
    ['$root.$q.screen.width']: {
      async handler(val) {
        if (val > 900 && !this.containerSideMenu)
          this.toogleContainerSideMenu(true);
      },
    },
  },

  methods: {
    ...mapActions(useAsana, [
      'createTask',
      'getCustomField',
      'getCustomFieldEnum',
      'createEnumOptionForCustomField',
    ]),
    ...mapActions(useGrowmodo, ['getPlatformInfo', 'formatPlatformIconSrc']),

    toogleContainerSideMenu(force = null) {
      this.containerSideMenu = force !== null ? force : !this.containerSideMenu;
    },

    validateData() {
      if (!this.task || isNaN(this.step) || !this.task_data) return;
      if (
        (this.task_data[this.step] && this.task[this.step]) ||
        (this.task_data[this.step] &&
          this.task_data[this.step]?.required === false)
      )
        return true;
      return false;
    },

    previousStep() {
      this.$refs?.['formStepper']?.previous?.();
    },

    nextStep() {
      if (this.validateData()) this.$refs?.['formStepper']?.next?.();
    },

    gotoStep(step_no) {
      this.$refs?.['formStepper']?.goTo?.(step_no);
    },

    getTask(step_no) {
      return this.task_data[step_no] && this.task[step_no]
        ? this.task[step_no]
        : '';
    },

    async getOrCreateCustomFieldEnum(
      customFieldName,
      customFieldEnumValue,
      options = { payloadOpt: {}, noCreate: false }
    ) {
      let field_gid = await this.getCustomField({
        name: customFieldName,
        key: 'gid',
      });

      let customFieldEnum = await this.getCustomFieldEnum({
        gid: field_gid,
        enum_name: customFieldEnumValue,
      });
      // Check if customFieldEnumValue Exist on Enum
      if (!options?.noCreate && !customFieldEnum?.gid) {
        // If Not, Add the New customFieldEnumValue to Enum Options and Get the GID
        await this.createEnumOptionForCustomField({
          customFieldGid: field_gid,
          payload: {
            ...options?.payloadOpt,
            enabled: true,
            name: customFieldEnumValue,
          },
        }).then((res) => {
          customFieldEnum = res?.response;
        });
      }
      return {
        success: customFieldEnum?.gid ? 1 : 0,
        gid: field_gid,
        enumOpt: customFieldEnum,
      };
    },

    async submitTask() {
      if (!this.validateData()) return;

      // Test Success Message
      this.task_submitted = true;
      this.step = 100; // Done Steps
      if ('Test Success Message') return;

      this.task_submitting = true;
      const notif = this.$q.notify({
        group: false, // required to be updatable
        spinner: true,
        progress: false,
        message: 'Connecting to Asana',
        timeout: 0,
      });
      let percentage = 0;
      const interval = setInterval(() => {
        percentage = Math.min(99, percentage + Math.floor(Math.random() * 15));
        notif({
          caption: `Loading... ${percentage}%`,
        });
        if (percentage === 99) {
          notif({
            message: 'Verifying Response',
            caption: undefined,
          });
          clearInterval(interval);
        }
      }, 500);
      try {
        let selected_platform =
          this.getPlatformInfo(this.getTask(4), 'label') || 'No Preference';
        let selected_priority = this.getTask(3);
        let cf_taskType = await this.getOrCreateCustomFieldEnum(
          'Task Type',
          this.taskInfo?.type || 'Tech',
          {
            noCreate: true,
          }
        );
        let cf_priority = await this.getOrCreateCustomFieldEnum(
          'Priority',
          selected_priority?.name,
          {
            noCreate: true,
          }
        );
        let cf_platform = await this.getOrCreateCustomFieldEnum(
          'Platform',
          selected_platform
        );
        // Formated Request
        const payload_formated = {
          name: this.taskInfo.label,
          brand: this.brand,
          projects: this.project,
          assignee: this.assignee,
          approval_status: 'pending',
          assignee_status: 'upcoming',
          tags: [],
          completed: false,
          custom_fields: {
            [cf_taskType?.gid]: cf_taskType?.enumOpt?.gid,
            [cf_priority?.gid]: cf_priority?.enumOpt?.gid,
            [cf_platform?.gid]: cf_platform?.enumOpt?.gid,
          },
          notes: 'Created from Growmodo Client Portal',
          html_notes:
            `<body><strong>Task Name 🔖:</strong>\n` +
            this.taskInfo.label +
            `\n\n<strong>Description 🎯:</strong>\n` +
            this.getTask(1) +
            `\n\n<strong>Target Audience 👤:</strong>\n` +
            this.getTask(2) +
            `\n\n<strong>Priority 🥇:</strong>\n` +
            selected_priority?.name +
            `\n\n<strong>Platform 🗂️:</strong>\n` +
            selected_platform +
            `\n\n<strong>Warning 🚨:</strong>\n` +
            this.getTask(6) +
            `\n\n<strong>Resources 📦:</strong>\n` +
            this.getTask(8) +
            `</body>`,
        };
        // Send Request Task
        this.createTask(payload_formated)
          .then((result) => {
            if (result.success) {
              if (process.env.DEV_MODE) console.info(result);
              notif({
                icon: 'icon-check-circle-broken',
                spinner: false,
                message: 'Task Created!',
                caption: 'GID: ' + result?.response?.gid,
                timeout: 5000,
                actions: [
                  {
                    label: 'Open Asana',
                    size: 'md',
                    color: 'primary',
                    class: 'bg-grey-3',
                    noCaps: true,
                    handler: () => {
                      window.open(result?.response?.permalink_url, '_blank');
                    },
                  },
                ],
              });
              this.task_submitted = true;
              this.step = 100; // Done Steps
              // // Close Form Task
              // this.$root.toggleForm_Task('', false);
              // // Close Task Directory
              // this.$root.toggleRequest('task', false);
              // // Close Request Options
              // this.$root.toggleRequest('', false);
            } else {
              if (process.env.DEV_MODE) console.warn(result);
              this.task_submitted = false;
              notif({
                icon: 'icon-alert-triangle',
                spinner: false,
                message: 'Request Failed!',
                caption: undefined,
                timeout: 2000,
              });
            }
          })
          .catch((err) => {
            if (process.env.DEV_MODE) console.error(err);
            this.task_submitted = false;
            notif({
              icon: 'icon-alert-circle',
              spinner: false,
              message: 'Request Error!',
              caption: result?.response?.message,
              timeout: 2000,
            });
          })
          .finally(() => {
            // Disable processing status
            this.task_submitting = false;
            notif({
              progress: true,
            });
            // Clear Interval
            clearInterval(interval);
          });
      } catch (e) {
        if (process.env.DEV_MODE) console.error(e);
        // Disable processing status
        this.task_submitting = false;
        notif({
          icon: 'icon-alert-circle',
          spinner: false,
          message: 'Form Error!',
          timeout: 2000,
          progress: true,
        });
        // Clear Interval
        clearInterval(interval);
      }
    },

    filterBrandsFn(val, update) {
      update(() => {
        const newOpt = {
          id: 0,
          label: 'Add New Brand',
          icon: 'icon-plus',
          action: () => this.addNewBrand(),
        };

        if (val === '') {
          this.filteredBrandsOptions = Object.values(
            this.selectedOrgBrands
          ).concat(newOpt);
        } else {
          let _query = searchInObjectArray(this.filteredBrandsOptions, val);
          this.filteredBrandsOptions = _query.concat(newOpt);
        }
      });
    },

    goTo_TaskDirectory() {
      // Open Task Directory
      this.$root.toggleRequest('project', false);
      this.$root.toggleRequest('task', true);
      // Close this form
      this.$root.toggleForm_Task(false);
    },

    goTo_Home() {
      // Add Go To Home
      this.goHome = true;
      // Close Request Option
      this.$root.toggleRequest('', false);
      // Close Task Directory
      this.$root.toggleRequest('task', false);
      // Close Project Directory
      this.$root.toggleRequest('project', false);
      // Close this Form
      this.$root.toggleForm_Task(false);
      // Navigate to Home
      this.$router.push('/');
    },

    addNewBrand() {
      this.addBrand = true;
    },
  },

  beforeUnmount() {
    this.$root.task.info = {};
    if (!this.goHome) {
      this.$root.setMeta({ title: this.prevTitle });
    } else {
      this.$root.setMeta({ title: 'Home' });
    }
  },
});
</script>
