<template>
  <q-layout container view="LHh lpR lFf" class="bg-color">
    <q-header class="bg-color">
      <q-toolbar class="full-width bg-transparent q-pa-lg">
        <div
          v-close-popup
          class="text-primary font-medium cursor-pointer row items-center"
          @click="goTo_ProjectDirectory"
        >
          <q-icon
            size="sm"
            name="icon-arrow-narrow-left"
            class="color-primary2 q-mr-sm"
          />
          Back to Project Directory
        </div>

        <q-space />
        <div
          class="absolute-top text-left"
          :style="$q.screen.sm ? 'margin-top: 150px' : 'margin-top: 24px'"
          style="max-width: 500px; margin-left: auto; margin-right: auto"
        >
          <div
            class="row items-center justify-center dnav no-wrap"
            :class="$q.screen.sm ? 'q-px-xl' : ''"
          >
            <template v-for="i in slideCount" :key="i">
              <q-btn
                flat
                round
                size="xs"
                icon="icon-circle-01"
                class="col dicon"
                @click="
                  () =>
                    slideNumber == i - 1 ||
                    slideNumber == i ||
                    slideNumber == i - 2
                      ? gotoSlideName(slides[i - 1])
                      : void 0
                "
                :class="
                  (slideNumber >= i - 1
                    ? slideNumber == i - 1
                      ? 'active'
                      : 'past'
                    : '') + ($q.screen.xs ? ' dicon-mini bg-none' : '')
                "
              >
                <tooltip-pro
                  arrow="top"
                  anchor="bottom middle"
                  self="top middle"
                  :offset="[10, 10]"
                  bgColor="white"
                  color="primary"
                  shadow="1"
                >
                  {{ slides[i - 1] }}
                </tooltip-pro>
              </q-btn>
              <div
                v-if="i !== slideCount"
                class="col-6 dline"
                :class="
                  (slideNumber >= i
                    ? slideNumber == i
                      ? 'active'
                      : 'past'
                    : '') +
                  ($q.screen.sm ? ' dline-mini' : '') +
                  ($q.screen.xs ? ' dline-mini-xs' : '')
                "
              ></div>
            </template>
          </div>
        </div>
        <q-space />

        <div class="row items-center justify-end text-right">
          <div
            :class="$q.screen.sm ? 'col-12 row justify-end' : 'q-mr-sm'"
            class="color-primary2"
          >
            Project Type:
          </div>
          <div
            class="cursor-pointer font-medium text-primary"
            :class="$q.screen.sm ? 'col-12 row justify-end' : ''"
          >
            {{ projectInfo.title }}
          </div>
        </div>

        <div class="absolute-top-right q-ma-sm" v-if="!noclose">
          <close-button />
        </div>
      </q-toolbar>
    </q-header>

    <q-page-container>
      <q-page padding>
        <q-card flat class="bg-color q-pt-xl">
          <q-card-section class="flex flex-center">
            <div style="max-width: 650px">
              <q-carousel
                animated
                ref="formSlider"
                height="600px"
                :navigation="false"
                navigation-position="top"
                v-model="slidesModel"
                transition-prev="slide-right"
                transition-next="slide-left"
                class="bg-transparent text-primary rounded full-width"
              >
                <template v-slot:navigation-icon="{ active, onClick, index }">
                  <q-btn
                    flat
                    round
                    dense
                    size="sm"
                    icon="icon-circle-01"
                    :color="
                      active || index <= slides.indexOf(slidesModel)
                        ? 'secondary'
                        : 'deep-purple-2'
                    "
                    class=""
                    @click="onClick"
                  />
                </template>

                <!-- Description -->
                <q-carousel-slide
                  :name="slides[0]"
                  class="column no-wrap q-pt-xl"
                >
                  <div class="full-width text-left q-pb-lg">
                    <div class="font-bold text-h5 q-py-sm">
                      Project Description
                    </div>
                    <div class="text-body2">
                      {{ lorem }}
                    </div>
                  </div>
                  <div class="full-width">
                    <q-input
                      outlined
                      autofocus
                      rows="15"
                      type="textarea"
                      v-model="project[slides[0]]"
                      :name="slides[0]"
                      class="rounded text-body2 input-white"
                      :rules="[
                        (val) =>
                          (val &&
                            val !== '' &&
                            val !== null &&
                            val.length > 1) ||
                          '',
                      ]"
                    />
                  </div>
                </q-carousel-slide>

                <!-- Platform -->
                <q-carousel-slide
                  :name="slides[1]"
                  class="column no-wrap q-pt-xl"
                >
                  <div class="full-width text-left q-pb-lg">
                    <div class="font-bold text-h5 q-py-sm">Select Platform</div>
                    <div class="text-body2">
                      {{ lorem }}
                    </div>
                  </div>
                  <div class="full-width">
                    <div class="row q-gutter-lg q-pb-md">
                      <q-radio
                        v-for="(platform, i) in projectInfo.platforms"
                        :key="platform"
                        v-model="project[slides[1]]"
                        :name="slides[1]"
                        :val="platform"
                        size="lg"
                        color="secondary"
                        :checked-icon="formatPlatformIconSrc(platform)"
                        :unchecked-icon="formatPlatformIconSrc(platform)"
                        class="field-select bg-none text-body1 font-medium color-primary2 rounded q-py-sm q-px-md"
                        style="min-height: 70px"
                      >
                        <span class="q-pr-md">{{
                          getPlatformInfo(platform, 'title')
                        }}</span>
                        <tooltip-pro
                          v-if="i == 0"
                          anchor="bottom middle"
                          self="center middle"
                          bgColor="transparent"
                          color="primary"
                          :offset="[0, $q.screen.xs ? 10 : 15]"
                          style="opacity: 0.4"
                          v-model="platformRecommended"
                          @before-hide="beforeHide"
                        >
                          Recommended
                        </tooltip-pro>
                      </q-radio>
                      <q-radio
                        v-model="project[slides[1]]"
                        :name="slides[1]"
                        size="lg"
                        color="secondary"
                        val="no-preference"
                        label="No Preference"
                        class="field-select field-select-none bg-none text-body1 font-medium color-primary2 rounded q-py-sm q-px-md"
                        style="min-height: 70px"
                      />
                    </div>
                  </div>
                </q-carousel-slide>

                <!-- Video Walkthrough -->
                <q-carousel-slide
                  :name="slides[2]"
                  class="column no-wrap q-pt-xl"
                >
                  <div class="full-width text-left q-pb-lg">
                    <div class="font-bold text-h5 q-py-sm">
                      Video Walkthrough
                    </div>
                    <div class="text-body2">
                      {{ lorem }}
                    </div>
                  </div>
                  <div class="full-width">
                    <q-input
                      outlined
                      autofocus
                      rows="5"
                      type="textarea"
                      v-model="project[slides[2]]"
                      :name="slides[2]"
                      class="rounded text-body2 input-white"
                      :rules="[(val) => true]"
                    />
                  </div>
                </q-carousel-slide>

                <!-- Warning -->
                <q-carousel-slide
                  :name="slides[3]"
                  class="column no-wrap q-pt-xl"
                >
                  <div class="full-width text-left q-pb-lg">
                    <div class="font-bold text-h5 q-py-sm">Warning</div>
                    <div class="text-body2">
                      {{ lorem }}
                    </div>
                  </div>
                  <div class="full-width">
                    <q-input
                      outlined
                      autofocus
                      rows="15"
                      type="textarea"
                      v-model="project[slides[3]]"
                      :name="slides[3]"
                      class="rounded text-body2 input-white"
                      :rules="[(val) => true]"
                    />
                  </div>
                </q-carousel-slide>

                <!-- Resources -->
                <q-carousel-slide
                  :name="slides[4]"
                  class="column no-wrap q-pt-xl"
                >
                  <div class="full-width text-left q-pb-lg">
                    <div class="font-bold text-h5 q-py-sm">Resources</div>
                    <div class="text-body2">
                      {{ lorem }}
                    </div>
                  </div>
                  <div class="full-width">
                    <q-input
                      outlined
                      autofocus
                      rows="15"
                      type="textarea"
                      v-model="project[slides[4]]"
                      :name="slides[4]"
                      class="rounded text-body2 input-white"
                      :rules="[(val) => true]"
                    />
                  </div>
                </q-carousel-slide>
              </q-carousel>

              <!-- Navigation Buttons -->
              <div
                class="row items-center justify-center q-px-lg q-pt-none q-mt-none q-col-gutter-md"
              >
                <div>
                  <q-btn
                    v-if="slideNumber > 0"
                    outline
                    no-caps
                    label="Back"
                    padding="md"
                    color="primary"
                    style="width: 150px"
                    @click="previousSlide()"
                    :ripple="{ color: 'secondary' }"
                    :disable="project_submitting"
                    class="bg-none text-primary font-medium text-body1 rounded"
                  />
                </div>
                <div>
                  <q-btn
                    no-caps
                    :label="
                      slideNumber == slides.length - 1 ? 'Submit' : 'Next'
                    "
                    type="submit"
                    padding="md"
                    color="primary"
                    style="width: 150px"
                    @click="nextSlide()"
                    :ripple="{ color: 'secondary' }"
                    :disable="project_submitting"
                    class="bg-primary text-white font-medium text-body1 rounded"
                  />
                </div>
              </div>
            </div>
          </q-card-section>
        </q-card>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<script>
import { defineComponent } from 'vue';
import { mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'FormProjectCreate',

  props: {
    noclose: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    const project_data = [
      {
        label: 'Description',
        required: true,
      },
      {
        label: 'Platform',
        required: true,
      },
      {
        label: 'Walkthrough',
        required: false,
      },
      {
        label: 'Warning',
        required: false,
      },
      {
        label: 'Resources',
        required: false,
      },
    ];
    return {
      prevTitle: document.title,
      lorem:
        'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Itaque voluptatem totam, architecto cupiditate officia rerum, error dignissimos praesentium libero ab nemo.',
      platformRecommended: false,
      slidesModel: project_data[0].label,
      project: {},
      project_data,
      project_submitting: false,
      project_submitted: undefined,
    };
  },

  async mounted() {
    this.prevTitle = this.$root?.config?.metaData?.title;
    this.$root.setMeta({ title: this.projectInfo.title });
  },

  computed: {
    slides() {
      return this.project_data.map((d) => d.label);
    },

    projectInfo() {
      return this.$root.project.info;
    },

    slideNumber() {
      return this.slides.indexOf(this.slidesModel);
    },

    slideCount() {
      return this.slides.length;
    },
  },

  watch: {
    slidesModel: {
      async handler(val) {
        if (val == this.slides[1]) {
          setTimeout(() => {
            if (this.slideNumber == 1) {
              this.platformRecommended = true;
            } else {
              this.platformRecommended = false;
            }
          }, 500);
        } else {
          this.platformRecommended = false;
        }
      },
    },
  },

  methods: {
    ...mapActions(useGrowmodo, ['getPlatformInfo', 'formatPlatformIconSrc']),

    goTo_ProjectDirectory() {
      // Open Task Directory
      this.$root.toggleRequest('task', false);
      this.$root.toggleRequest('project', true);
      // Close this form
      this.$root.toggleForm_Project(false);
    },

    validateData() {
      if (!this.project || isNaN(this.slideNumber) || !this.slides) return;
      if (
        this.project[this.slides[this.slideNumber]] ||
        this.project_data[this.slideNumber].required === false
      )
        return true;
      return false;
    },

    previousSlide() {
      this.$refs.formSlider.previous();
    },

    nextSlide() {
      if (!this.validateData()) return;
      if (this.slideNumber != this.slides.length - 1) {
        this.$refs.formSlider.next();
      } else {
        this.submitProject();
      }
    },

    submitProject() {
      console.log('Project submitted');
    },

    gotoSlideName(name) {
      if (!this.validateData()) return;
      this.$refs.formSlider.goTo(name);
    },

    beforeHide() {
      if (this.slidesModel == this.slides[1]) {
        this.platformRecommended = true;
      }
    },
  },

  beforeUnmount() {
    if (this.project_submitted !== true)
      this.$root.setMeta({ title: this.prevTitle });
  },
});
</script>
