<template>
  <q-layout
    container
    view="LHh lpR lFf"
    class="bg-white full-width full-height"
  >
    <q-drawer
      bordered
      show-if-above
      v-model="containerSideMenu"
      side="left"
      :width="275"
      :breakpoint="850"
      :overlay="$q.screen.width <= 800"
      class="bg-white"
    >
      <q-scroll-area class="fit">
        <q-list class="q-pt-xl q-mx-lg">
          <q-item class="text-primary q-px-none">
            <q-item-section class="text-body2 font-medium">
              {{ title }}
            </q-item-section>
          </q-item>

          <q-item
            clickable
            v-ripple:secondary
            class="text-body2 font-medium q-my-xs hover-text-secondary rounded hover-bg-accent"
            :class="
              activeFilter == '' || activeFilter == 'all'
                ? 'text-secondary font-medium bg-accent'
                : 'text-primary'
            "
            @click="updateFilter('')"
          >
            <q-item-section avatar style="min-width: unset">
              <q-icon name="icon-all-categories" size="sm" />
            </q-item-section>
            <q-item-section>All Categories</q-item-section>
          </q-item>

          <q-item
            clickable
            v-ripple:secondary
            class="text-body2 font-medium q-my-xs hover-text-secondary rounded hover-bg-accent"
            v-for="(item, i) in itemsDirectory"
            :key="i"
            :class="
              activeFilter == item.tag
                ? 'text-secondary font-medium bg-accent'
                : 'text-primary'
            "
            @click="updateFilter(item.tag)"
          >
            <q-item-section avatar style="min-width: unset">
              <q-icon size="sm" :name="item.icon || 'icon-browser'" />
            </q-item-section>
            <q-item-section>{{ item.title }}</q-item-section>
          </q-item>
        </q-list>
      </q-scroll-area>
    </q-drawer>

    <q-header class="bg-white">
      <q-toolbar class="q-px-none">
        <div class="q-px-md q-pt-md q-pb-none" v-if="$q.screen.width <= 850">
          <q-btn
            flat
            dense
            size="lg"
            icon="icon-chevron-left"
            class="text-grey-6 hover-text-secondary hover-bg-accent rounded"
            @click="toogleContainerSideMenu()"
          >
            <tooltip-pro anchor="center end" self="center left">
              Show Menu
            </tooltip-pro>
          </q-btn>
        </div>
        <q-space />
        <div v-if="!noclose" class="q-px-md q-pt-md q-pb-none">
          <close-button />
        </div>
      </q-toolbar>

      <div
        class="row justify-center items-center full-width q-px-md"
        :class="$q.screen.sm || $q.screen.lt.sm ? 'q-py-md' : 'q-pb-md'"
      >
        <SearchDropdown
          :requestOptions="null"
          placeholder="What is your next request?"
          @searching="logKey"
          @blur="searchFilter = ''"
        />
      </div>
    </q-header>

    <q-page-container>
      <q-page padding>
        <q-infinite-scroll @load="loadInfiniteScroll" :offset="100">
          <div
            v-if="!visibleItemsCount || visibleItemsCount < 1"
            class="row items-center contents-center full-width full-height text-center q-py-md"
            style="height: calc(100vh - 300px) !important"
          >
            <div class="col-12">
              <div>
                <q-icon
                  size="10rem"
                  name="icon-alert-circle"
                  class="q-pr-sm text-grey-3"
                />
              </div>
              <div class="text-h5 text-grey-6">No Available Options</div>
            </div>
          </div>

          <div
            v-else
            class="row q-pa-md q-col-gutter-lg"
            :style="bottomFade ? 'margin-bottom: 200px' : ''"
          >
            <transition
              appear
              enter-active-class="animated zoomIn"
              leave-active-class="animated zoomOut"
              v-for="(item, i) in itemsList"
              :key="i"
            >
              <div
                class="col-12 col-md-6 col-lg-4"
                v-if="
                  activeFilter === '' || activeFilter === 'all'
                    ? true
                    : item.tags
                    ? item.tags.includes(activeFilter)
                    : false
                "
              >
                <q-card
                  flat
                  bordered
                  v-ripple:accent
                  class="card-quicklinks full-height hover-border-secondary row"
                >
                  <q-card-section
                    class="q-px-lg q-pt-lg q-pb-none"
                    @click="openItem(item)"
                  >
                    <CardIconBox
                      iconSize="md"
                      :icon="
                        item?.icon ||
                        itemsDirectory?.[item?.tags?.[0]]?.icon ||
                        'icon-browser'
                      "
                    />
                    <div
                      v-if="item.turn_arround_days || item.type"
                      class="q-pt-lg"
                    >
                      <q-badge rounded class="font-medium badge badge-blue">
                        {{
                          item.turn_arround_days
                            ? item.turn_arround_days + ' Avg. Turnaround'
                            : item.type
                        }}
                      </q-badge>
                    </div>
                    <div class="text-h5 font-medium q-pt-sm q-pb-md">
                      {{ item.title }}
                    </div>
                    <div class="text-body1 color-primary2">
                      {{
                        item.description ||
                        'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.'
                      }}
                    </div>
                  </q-card-section>

                  <q-card-section
                    v-if="item.platforms && item.platforms.length"
                    class="q-px-lg q-pt-md q-pb-lg full-width"
                  >
                    <div class="q-pr-sm text-body2 font-medium color-primary2">
                      Platform{{
                        item.platforms && item.platforms.length > 1 ? 's' : ''
                      }}
                    </div>
                    <div class="q-pt-sm">
                      <q-scroll-area
                        visible
                        :bar-style="{
                          opacity: 0,
                          paddingTop: '50px',
                        }"
                        :thumb-style="{
                          opacity: 0,
                          paddingTop: '50px',
                          'pointer-events': 'none !important',
                        }"
                        style="height: 40px; max-width: 100%"
                        @mouseover="hoverHere"
                        @mousemove="hoverHere"
                        @mouseout="hoverEnd"
                      >
                        <div
                          v-if="false"
                          style="height: 30px; width: 75px"
                          class="fixed-left bg-gradient-left"
                        ></div>
                        <div
                          class="row no-wrap q-gutter-sm no-pointer-events q-pr-xl"
                        >
                          <template
                            v-for="platform in item.platforms"
                            :key="platform"
                          >
                            <q-badge
                              rounded
                              class="bg-grey-2 text-primary q-px-sm q-py-xs text-caption font-medium"
                            >
                              <q-icon
                                :name="formatPlatformIconSrc(platform)"
                                class="q-pr-xs"
                              />
                              {{
                                getPlatformInfo(platform, 'title') || platform
                              }}
                            </q-badge>
                          </template>
                        </div>
                        <div
                          style="height: 30px; width: 75px"
                          class="fixed-right bg-gradient-right"
                        ></div>
                      </q-scroll-area>
                    </div>
                  </q-card-section>
                </q-card>
              </div>
            </transition>
          </div>

          <div
            v-if="bottomFade"
            class="full-width fixed-bottom bg-gradient no-pointer-events"
            style="height: 300px"
          ></div>

          <template v-slot:loading>
            <div
              class="row justify-center q-my-md relative-position"
              style="z-index: 7"
            >
              <q-spinner-dots color="secondary" size="40px" />
            </div>
          </template>
        </q-infinite-scroll>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<script>
import { defineComponent } from 'vue';
import { searchInObjectArray } from 'src/assets/scripts/functions';
import SearchDropdown from 'src/components/Search/CustomDropdown.vue';
import { mapState, mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';

export default defineComponent({
  name: 'RequestItem',

  components: { SearchDropdown, CardIconBox },

  props: {
    bottomFade: {
      type: Boolean,
      default: false,
    },
    noclose: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: 'Item Directory',
    },
    directoryType: {
      type: String,
      default: 'task', // task or project
    },
  },

  watch: {
    activeFilter: {
      async handler(val) {
        this.itemsList = this.visibleItems.slice(0, 10);
      },
    },
  },

  data() {
    return {
      prevTitle: document.title,
      containerSideMenu: false,
      activeFilter: '',
      itemsList: [],
      searchFilter: '',
      scrollHoverInterval: null,
    };
  },

  computed: {
    ...mapState(useGrowmodo, [
      'taskDirectory',
      'taskDirectory_Categories',
      'projectDirectory',
      'projectDirectory_Categories',
    ]),

    isTask() {
      return this.directoryType === 'task';
    },

    isProject() {
      return this.directoryType === 'project';
    },

    items() {
      return Object.values(
        this.isTask ? this.taskDirectory : this.projectDirectory
      );
    },

    itemsDirectory() {
      return this.isTask
        ? this.taskDirectory_Categories
        : this.projectDirectory_Categories;
    },

    visibleItems() {
      return searchInObjectArray(this.itemWithFilter, this.searchFilter, [
        'label',
        'title',
        'description',
      ]);
    },

    itemWithFilter() {
      return this.activeFilter === '' || this.activeFilter === 'all'
        ? this.items
        : this.items.filter((e) => e.tags.includes(this.activeFilter));
    },

    visibleItemsCount() {
      return this.visibleItems ? this.visibleItems.length : 0;
    },
  },

  methods: {
    ...mapActions(useGrowmodo, ['formatPlatformIconSrc', 'getPlatformInfo']),

    openItem(item) {
      if (!item) return;
      this.$root?.[this.isTask ? 'toggleForm_Task' : 'toggleForm_Project']?.(
        item
      );
    },

    updateFilter(filter) {
      this.activeFilter = filter;
    },

    toogleContainerSideMenu(force = null) {
      this.containerSideMenu = force !== null ? force : !this.containerSideMenu;
    },

    loadInfiniteScroll(index, done) {
      if (this.visibleItems.length <= this.itemsList.length) {
        done();
      }
      setTimeout(() => {
        let newItems = this.visibleItems.slice(
          this.itemsList.length,
          this.itemsList.length + 10
        );
        newItems.forEach((e) => {
          this.itemsList.push(e);
        });
        done();
      }, 1000);
    },

    logKey({ val, update }) {
      this.searchFilter = val;
    },

    hoverHere(evt) {
      if (this.scrollHoverInterval) {
        this.hoverEnd();
        this.scrollHoverInterval = null;
      }
      let ellayerX = evt.layerX || 0;
      const origTarget = evt.target,
        parent = origTarget?.parentElement;
      let elOffsetLeft = origTarget?.offsetLeft || 0,
        elWidth = origTarget?.offsetWidth || 0,
        slideLeft = ellayerX - elOffsetLeft < elWidth / 2;
      let target = parent?.querySelector?.('.q-scrollarea__container');
      if (!this.scrollHoverInterval && target) {
        this.scrollTarget(target, slideLeft);
      }
    },

    async scrollTarget(target, slideLeft) {
      this.scrollHoverInterval = setInterval(async () => {
        let newScrollVal = 0;
        if (!slideLeft) {
          newScrollVal =
            target.scrollLeft + 10 >= target.scrollLeftMax
              ? target.scrollLeftMax
              : target.scrollLeft + 10;
        } else {
          newScrollVal = target.scrollLeft <= 0 ? 0 : target.scrollLeft - 10;
        }
        // console.log({
        //   direction: slideLeft ? 'Left' : 'Right',
        //   max: target.scrollLeftMax || target.offsetWidth,
        //   new_scroll: newScrollVal,
        //   current_scroll: target.scrollLeft,
        // });
        target.scrollTo({
          top: 0,
          left: newScrollVal,
          behavior: 'smooth',
        });
      }, 10);
    },

    hoverEnd() {
      if (this.scrollHoverInterval) {
        try {
          clearInterval(this.scrollHoverInterval);
        } catch (e) {}
      }
    },
  },

  async mounted() {
    this.prevTitle = this.$root?.config?.metaData?.title;
    this.$root.setMeta({ title: this.title });
    this.itemsList = this.visibleItems.slice(0, 10);
  },

  beforeUnmount() {
    this.hoverEnd();
    this.$root[this.isTask ? 'task' : 'project'].info = {};
    this.$root.setMeta({ title: this.prevTitle });
  },
});
</script>
