<template>
  <q-card flat class="rounded q-pa-sm">
    <q-card-section>
      <q-toolbar class="bg-transparent q-pa-none">
        <q-icon :name="formatPlatformIconSrc(options?.icon)" size="xl" />
        <div class="absolute-top-right" v-if="!noclose">
          <close-button />
        </div>
      </q-toolbar>
    </q-card-section>
    <q-card-section class="q-pt-none">
      <div class="text-h5 font-demi-bold letter-space-normal">
        {{ options?.message }}
      </div>
    </q-card-section>
    <q-card-actions align="right" class="full-width q-gutter-sm q-py-md">
      <q-btn outline no-caps v-close-popup color="primary" class="col rounded">
        <span class="text-body1 font-medium">Cancel</span>
      </q-btn>
      <q-btn
        no-caps
        unelevated
        color="primary"
        class="col rounded slide-hover"
        :href="options?.url"
      >
        <span class="text-body1 font-medium">Okay</span>
        <q-icon
          name="icon-arrow-narrow-right"
          size="xs"
          class="slide-hover-left-md q-ml-sm"
        />
      </q-btn>
    </q-card-actions>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'RedirectConfirmation',

  props: {
    noclose: {
      type: Boolean,
      default: false,
    },
    options: {
      type: Object,
      default: () => {},
    },
  },

  data() {
    return {
      goHome: false,
      prevTitle: document.title,
    };
  },

  async mounted() {
    this.prevTitle = this.$root?.config?.metaData?.title;
    if (this.options?.title) this.$root.setMeta({ title: this.options?.title });
  },

  methods: {
    ...mapActions(useGrowmodo, ['formatPlatformIconSrc']),
  },

  beforeUnmount() {
    if (!this.goHome) {
      this.$root.setMeta({ title: this.prevTitle });
    } else {
      this.$root.setMeta({ title: 'Home' });
    }
  },
});
</script>
