<template>
  <q-card flat class="rounded q-pa-sm">
    <q-card-section v-if="!noclose" class="text-right">
      <close-button />
    </q-card-section>
    <q-card-section
      class="text-center bg-transparent q-px-lg"
      :class="noclose ? 'q-pt-md' : 'q-py-none'"
    >
      <div class="text-h4 text-primary font-medium">Change your plan</div>
    </q-card-section>

    <q-card-section class="q-px-lg q-pt-lg q-pb-none">
      <div class="q-pb-md">
        <div class="text-body2 font-medium color-primary2">
          Your current plan
        </div>
        <card-item
          v-ripple="false"
          class="badge-bg-active badge-border-active q-my-sm cursor-pointer border-add hover-border-secondary"
        >
          <template v-slot:title>
            <div class="row q-col-gutter-x-lg items-center">
              <div class="col-auto letter-space-normal">
                {{ subscription.label }} {{ subscription_talent_id }}
                <div class="q-py-xs text-body2 font-medium badge-text-active">
                  <span>
                    {{
                      planPriceCalculator(
                        subscription_talent_id,
                        subscription_billing_id
                      )?.label
                    }}
                  </span>
                  <template>{{
                    subscription_BillingTypes[subscription_billing_id]
                  }}</template>
                </div>
              </div>
              <div class="col-auto text-body2 text-primary letter-space-normal">
                {{ subscription.description_1 }}
                {{
                  subscription_talent_id === -1
                    ? 'custom'
                    : numberToWords(subscription_talent_id)
                }}
                {{
                  subscription_talent_id > 1 || subscription_talent_id === -1
                    ? 'users'
                    : 'user'
                }}.
              </div>
            </div>
          </template>
        </card-item>
      </div>
      <div class="q-pb-md">
        <div class="text-body2 font-medium color-primary2">
          Days used on your current plan
        </div>
        <div class="q-my-sm">
          <RemainingDaysProgress />
        </div>
      </div>
      <div class="q-pb-md">
        <div class="text-body2 font-medium color-primary2">Your new plan</div>
        <div>
          <div class="row q-col-gutter-md">
            <div
              v-for="item in subscription_Talents"
              :key="item"
              class="col-12 col-sm-6 col-md-4"
            >
              <q-card
                flat
                v-ripple="{ color: 'secondary' }"
                class="rounded border-add border-md text-body2 hover-border-secondary"
                :class="
                  newPlan['talent']?.value === item.value
                    ? 'border-secondary bg-accent'
                    : ''
                "
              >
                <q-radio
                  v-model="newPlan['talent']"
                  v-ripple="{ color: 'secondary' }"
                  :val="item"
                  color="secondary"
                  class="fit q-py-xs q-px-sm"
                >
                  {{ item.label }}
                </q-radio>
              </q-card>
            </div>
          </div>
        </div>
      </div>
      <div class="q-pb-md">
        <div class="text-body2 font-medium color-primary2">Billing</div>
        <div class="q-my-sm">
          <div class="row q-col-gutter-md">
            <div
              v-for="item in subscription_BillingTypes"
              :key="item"
              class="col-12 col-sm-6"
            >
              <q-card
                flat
                class="rounded border-add border-md text-body2 hover-border-secondary"
                :class="
                  newPlan['billing']?.id === item.id
                    ? 'border-secondary bg-accent'
                    : ''
                "
              >
                <q-radio
                  v-model="newPlan['billing']"
                  v-ripple="{ color: 'secondary' }"
                  :val="item"
                  color="secondary"
                  class="fit q-py-xs q-px-sm"
                >
                  {{ item.label }}
                  <q-badge
                    v-if="
                      item.savings &&
                      planPriceCalculator(
                        newPlan.talent?.value || 1,
                        item.id
                      )?.discount_label?.toLowerCase?.() !== 'custom'
                    "
                    class="badge badge-blue font-medium q-ml-xs"
                  >
                    {{ item.savings_label }}
                    {{
                      planPriceCalculator(newPlan.talent?.value || 1, item.id)
                        ?.discount_label
                    }}
                  </q-badge>
                </q-radio>
              </q-card>
            </div>
          </div>
        </div>
      </div>
      <div class="q-pb-md">
        <div class="text-body2 font-medium color-primary2">Starts on</div>
        <div class="q-my-sm flex inline">
          <q-input
            dense
            outlined
            unelevated
            v-model="newPlan.start_date"
            @change="startDateInput"
            placeholder="Select date"
            class="hover-text-secondary cursor-pointer"
            style="width: 140px"
          >
            <template v-slot:prepend>
              <q-icon
                name="icon-calendar"
                size="sm"
                class="hover-text-secondary"
              />
            </template>
            <q-popup-proxy
              transition-show="scale"
              transition-hide="scale"
              :offset="[-170, -100]"
            >
              <q-date
                today-btn
                first-day-of-week="1"
                mask="MMM DD, YYYY"
                color="secondary"
                years-in-month-view
                :options="yearFromToday"
                v-model="newPlan.start_date"
                :navigation-min-year-month="thisMonth"
                :navigation-max-year-month="after1Year"
              >
              </q-date>
            </q-popup-proxy>
          </q-input>
        </div>
      </div>
      <div class="q-pb-md">
        <div class="text-body2 font-medium color-primary2">
          Message (Optional)
        </div>
        <div class="q-my-sm">
          <q-input
            dense
            outlined
            rows="4"
            type="textarea"
            placeholder="Enter a description..."
            class="format-bold-light"
          />
        </div>
      </div>
    </q-card-section>

    <q-card-actions
      class="full-width justify-between q-px-lg q-pb-lg q-pt-none"
    >
      <div>
        <div class="text-h3 font-medium">
          {{
            planPriceCalculator(newPlan.talent?.value, newPlan.billing?.id)
              ?.label
          }}
        </div>
        <div class="text-subtitle1 letter-space-normal">
          {{ newPlan['billing']?.billed_label }}
        </div>
      </div>
      <div>
        <q-btn
          no-caps
          unelevated
          color="primary"
          class="rounded slide-hover"
          :disable="!allDataIsValid"
          @click="nextBtnClick"
        >
          <span class="q-pl-sm">Submit Change Request</span>
          <q-icon
            size="sm"
            name="icon-arrow-narrow-right"
            class="q-px-sm"
            :class="{ 'slide-hover-left-md': allDataIsValid }"
          />
        </q-btn>
      </div>
    </q-card-actions>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { formatMoney, numberToWords } from 'src/assets/scripts/functions';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import fnStore from 'src/stores/fn.store';
import { date } from 'quasar';
import RemainingDaysProgress from 'src/components/Helpers/Subscription/RemainingDays-Progress.vue';

export default defineComponent({
  name: 'Dialog_UpgradeYourPlan',

  emits: ['nextBtnClick'],

  components: { RemainingDaysProgress },

  props: {
    noclose: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      newPlan: { talent: {}, billing: {}, start_date: null },
      numberToWords,
    };
  },

  computed: {
    ...mapState(useUser, ['user', 'selectedOrg']),
    ...mapState(useGrowmodo, [
      'subscription',
      'subscription_BillingTypes',
      'subscription_Talents',
    ]),

    subscription_billing_id() {
      return this.orgsubscription?.subscription_billing_id;
    },

    subscription_talent_id() {
      return this.orgsubscription?.subscription_talent_id;
    },

    thisMonth() {
      return date.formatDate(this.$root.realtimeDate, 'YYYY/MM');
    },

    after1Year() {
      return date.formatDate(
        date.addToDate(this.$root.realtimeDate, { years: 1 }),
        'YYYY/MM'
      );
    },

    orgsubscription() {
      return !fnStore.getAuth() ? {} : this.selectedOrg?.subscriptions;
    },

    allDataIsValid() {
      return (
        this.newPlan?.talent?.value &&
        this.newPlan?.billing?.value &&
        this.newPlan?.start_date &&
        date.isValid(this.newPlan?.start_date) &&
        this.yearFromToday(
          date.formatDate(this.newPlan?.start_date, 'YYYY/MM/DD')
        )
      );
    },
  },

  mounted() {},

  methods: {
    ...mapActions(useGrowmodo, ['planPriceCalculator']),

    formatMoney(money, minimumFractionDigits = 0) {
      return formatMoney(money, { minimumFractionDigits });
    },

    nextBtnClick() {
      if (this.allDataIsValid) {
        this.$root.dialogRequestUpgrade = true;
        this.$emit('nextBtnClick', this.newPlan);
      }
    },

    yearFromToday(d) {
      return (
        d >= date.formatDate(this.$root.realtimeDate, 'YYYY/MM/DD') &&
        d <=
          date.formatDate(
            date.addToDate(this.$root.realtimeDate, { year: 1 }),
            'YYYY/MM/DD'
          )
      );
    },

    startDateInput(e) {
      if (date.isValid(e)) {
        this.newPlan.start_date = date.formatDate(e, 'MMM DD, YYYY');
      }
    },
  },
});
</script>
